// CONDENSED _____________________________________

.condensed-title {
    font-size: 0.7rem;
    height: 2rem;
    display: flex;
    margin: auto;
    padding-bottom: 0.5rem;
    justify-content: center;
    width: 200px;
    

    @media (min-width: $small-phone) {
        height: 1rem;

        width: $small-phone;
    }

    @media (min-width: $phone) {
        width: $phone;
    }

    @media (min-width: $wide-phone) {
        margin: auto;
        font-size: 1rem;
        height: 1rem;
        width: $wide-phone;
    }
}

.condensed-container {
    border: $border-standard;
    border-radius: $rounded-rectangle;
    background-color: $background-color;
    padding: 0.25rem;
    display: flex;
    flex-direction: column;
    align-items: center;
    width: fit-content;
    margin: auto;

    @media (min-width: $wide-phone) {
        padding: 1rem;
        flex-flow: row;
        align-items: flex-start;
        justify-content: center;
    }
}

.condensed-tracks {
    display: flex;
    flex-direction: column;
    align-items: flex-start;

    @media (min-width: $wide-phone) {
        flex-flow: row;
        align-items: flex-start;

    }
}


