    .condensed-track-bar {
        display: flex;
        margin-left: 0.5px;
        height: 2rem;
        width: 2px;
        padding: 0;
        border-radius: 1px;

        @media (min-width: $wide-phone) {
            margin-left: 0px;
            width: 1.5px;
        }

        @media (min-width: $small-tablet) {
            margin-left: 0px;
            width: 2px;
        }

        @media (min-width: $tablet) {
            margin-left: 0px;
            width: 2.5px;
            border-bottom-left-radius: 2px;
            border-bottom-right-radius: 2px;
        }

        @media (min-width: $desktop) {
