        }

        @media (min-width: $large-desktop) {
            margin-left: 1px;
            width: 3px;
        }

        @media (min-width: $XXL-desktop) {
            margin-left: 1px;
            width: 4px;
        }

        @media (min-width: $monitor) {
            margin-left: 1px;
            width: 5px;
        }

    }

